@import './../../styles/vars.scss';

.radio__input {
  appearance: none;
  opacity: 0;

  &:not(:disabled):not(:checked) + span:hover::before {
    border-color: $purple;
  }

  &:not(:disabled):focus-visible + span::before {
    box-shadow: 0 0 0 1px $purple;
  }

  &:not(:disabled):active + span::before {
    box-shadow: 0 0 0 1px $purple;
  }

  &:checked + span::before {
    border-color: $purple;
  }

  &:checked + span {
    color: $darkShade-75;
  }

  &:checked + span::after {
    display: block;
  }

  &:disabled + span::before {
    background-color: rgba($darkShade-25, 0.4);
  }
}

.radio__label {
  position: relative;
  display: flex;
  height: 20px;

  &-text {
    padding-left: 30px;
    line-height: initial;
    color: $darkShade-50;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      border: 1px solid $darkShade-25;
      border-radius: 10px;
      width: 20px;
      height: 20px;
    }

    &::after {
      content: '';
      position: absolute;
      left: 4px;
      top: 4px;
      display: none;
      border-radius: 10px;
      width: 12px;
      height: 12px;
      background: $purple_gradient;
    }
  }
}
